.headerProducts {
	padding: 30px 0;
	.title {
		font-family: 'Source Sans Pro' , sans-serif;
		font-size: 30px;
		color: #000;
		text-align: center;
		text-transform: uppercase;
		padding-bottom: 25px; }
	.wrap {
		text-align: center; }
	.navProducts {
		display: inline-block;
		li {
			list-style: none;
			float: left; }
		a {
			font-family: 'Roboto' , sans-serif;
			font-size: 16px;
			color: #34476b;
			font-weight: 300;
			text-decoration: none;
			padding: 0 18px;
			&:hover {
				text-decoration: underline;
				text-decoration-style: dotted; } } } }
.sort {
	background-color: #fafafa;
	.first>li {
		float: left;
		list-style: none;
		padding: 15px 0;
		cursor: pointer;
		box-sizing: border-box;
		position: relative;
		&:hover {
			background-color: #fff;
			border-right: 1px solid #e9e9e9;
			border-left: 1px solid #e9e9e9;
			margin-left: -2px;
			padding-left: 1px;
			&>span {
				border: 0;
				color: #b01b52; }
			&>.sortList {
				display: block; } }
		&>span {
			font-family: 'Roboto' , sans-serif;
			font-size: 14px;
			color: #666666;
			font-weight: 300;
			text-decoration: none;
			background-image: url('../images/icons/arrowBottom.png');
			background-repeat: no-repeat;
			background-position: right 10px center;
			padding: 0 40px 0 15px;
			border-right: 1px solid #e9e9e9; } }
	.countProduct {
		float: right !important;
		b {
			font-family: 'Roboto' , sans-serif;
			font-size: 14px;
			color: #666666;
			font-weight: 300;
			padding-right: 15px;
			padding-left: 10px; }
		span {
			border-right: 1px solid #e9e9e9;
			border-left: 1px solid #e9e9e9;
			padding: 15px 40px 15px 15px; } }
	.sortList {
		background-color: #fff;
		position: absolute;
		box-sizing: border-box;
		padding-left: 10px;
		overflow: hidden;
		min-width: 100%;
		display: none;
		left: 0;
		top: 48.5px;
		li {
			list-style: none;
			width: 105px;
			padding: 7px 0;
			.psevodCheckBox {
				width: 15px;
				height: 15px;
				border: 1px solid #e9e9e9;
				float: left;
				background-position: center;
				background-repeat: no-repeat;
				cursor: pointer;
				&[data-status="on"] {
					background-image: url('../images/icons/arrowPodpiska.png'); }
				&[data-status="off"] {} }
			input {
				display: none; }
			span {
				font-size: 14px;
				color: #666666;
				font-weight: 300;
				font-family: 'Roboto' , sans-serif;
				padding-left: 10px; } } } }
.sortPosition {
	padding: 20px 0 10px;
	.wrap {
		text-align: center;
		.chooseParams {
			display: inline-block; }
		.param {
			background-color: #b01b52;
			font-family: 'Roboto' , sans-serif;
			font-size: 14px;
			color: #fff;
			font-weight: 300;
			display: block;
			float: left;
			padding: 5px 5px 2px 10px;
			display: flex;
			margin: 5px;
			.rmParam {
				margin-left: 20px; } }
		.dropAll {
			font-family: 'Roboto' , sans-serif;
			font-size: 14px;
			color: #b01b52;
			font-weight: 300;
			display: block;
			float: left;
			padding: 5px 5px 2px 10px;
			display: flex;
			text-decoration: underline;
			text-decoration-style: dotted;
			margin: 5px;
			.rmParamR {
				margin-right: 10px; } } } }
.youWasSee {
	padding: 0px 0 50px;
	.container {
		border-top: 1px solid #e9e9e9;
		padding-top: 30px; }
	.title {
		font-family: 'Source Sans Pro' , sans-serif;
		font-size: 30px;
		color: #000;
		text-align: center;
		text-transform: uppercase;
		padding-bottom: 25px; }
	.owl-carousel {
		position: relative; }
	.owl-nav {
		position: absolute;
		top: 50%;
		margin-top: -17px;
		width: 100%; }
	.owl-prev , .owl-next {
		position: absolute;
		height: 35px;
		width: 20px;
		background-repeat: no-repeat;
		background-size: container; }
	.owl-prev {
		left: -30px;
		background-image: url('../images/icons/popularLeft.png'); }
	.owl-next {
		right: -30px;
		background-image: url('../images/icons/popularRight.png'); } }
.content {
	padding: 30px 0;
	.leftSector {
		width: 285px;
		min-height: 1px;
		float: left; }
	.rightSector {
		float: right;
		width: 885px; }
	.card {
		width: 255px;
		float: left;
		margin: 0 20px;
		position: relative;
		margin-bottom: 30px;
		min-height: 425px;
		&:hover {
			.psevdoCard {
				transition-duration: .5s;
				position: absolute;
				padding: 10px;
				width: 100%;
				height: inherit;
				top: 0;
				left: -10px;
				z-index: 1;
				box-shadow: 0 0 15px rgba(0,0,0,.1);
				background-color: #fff; }
			.size {
				display: block; }
			&.new:before ,.dropPrice {
				z-index: 5; } }
		&.new:before {
			position: absolute;
			top: 0;
			right: 0;
			content: "NEW";
			font-family: 'Roboto' , sans-serif;
			font-size: 14px;
			color: #fff;
			font-weight: 300;
			background-color: #00838f;
			width: 40px;
			padding: 4px 0;
			text-align: center;
			top: 10px; }
		.size {
			display: none;
			width: 100%;
			float: left;
			clear: both;
			margin-top: 10px;
			padding-top: 10px;
			border-top: 1px solid #e6e6e6;
			div {
				font-family: 'Roboto' , sans-serif;
				font-size: 14px;
				font-weight: 300;
				color: #333333; } }
		.dropPrice {
			position: absolute;
			top: 0;
			right: 0;
			content: "NEW";
			font-family: 'Roboto' , sans-serif;
			font-size: 14px;
			color: #fff;
			font-weight: 300;
			background-color: #b01b52;
			width: 40px;
			padding: 4px 0;
			text-align: center;
			top: 10px; }
		.infoProduct {
			float: left;
			width: 120px;
			.nameProduct {
				font-family: 'Roboto' , sans-serif;
				font-size: 16px;
				color: #333333; }
			.price {
				font-family: 'Roboto' , sans-serif;
				color: #000;
				font-size: 18px;
				margin-top: 10px;
				font-weight: 300; } }
		img {
			margin-bottom: 20px; }
		.addToCart {
			color: #c3567e;
			font-size: 14px;
			background-color: #f8f8f8;
			width: 110px;
			height: 30px;
			float: right;
			display: flex;
			justify-content: space-around;
			align-items: center;
			font-family: 'Roboto' , sans-serif;
			cursor: pointer; }
		.breakPrice {
			color: #000;
			font-size: 14px;
			font-family: 'Roboto' , sans-serif;
			font-weight: 300;
			display: inline-block;
			position: relative;
			margin-top: 10px;
			&:before {
				background-color: #b01b52;
				width: 100%;
				height: 1px;
				position: absolute;
				content: "";
				left: 0;
				top: 50%; } } }
	.pagination {
		width: 100%;
		float: left;
		clear: both;
		text-align: center;
		.wrap {
			display: inline-block;
			a {
				display: block;
				background-color: #f8f8f8;
				width: 35px;
				height: 35px;
				text-align: center;
				// if flex
				display: flex;
				justify-content: space-around;
				align-items: center;
				float: left;
				text-decoration: none;
				font-family: 'Roboto' , sans-serif;
				font-size: 16px;
				color: #b01b52;
				font-weight: 300;
				transition-duration: .2s;
				&:hover {
					box-shadow: 0 0 3px rgba(0,0,0,.3);
					position: relative; }
				&.active {
					background-color: transparent;
					color: #666666; } } } } }
